.manager-container {
  background-color: #a93439;
  color: #fff;
  min-height: 100vh;
}

.manager-header {
  background-color: #d13239;
  padding: 3rem;
}

.manager-title {
  font-family: 'Gotham Rounded Bold Italic', sans-serif;
  margin: 1rem;
}

.manager-buttons {
  text-align: center;
  padding: 1rem;
}

.manager-buttons button {
  width: -webkit-max-content;
  width: max-content;
  height: 7vh;
  font-family: 'Gotham Rounded Bold Italic', sans-serif;
  background-color: #fff;
  color: #a93439;
  border: #e84a52 solid 2px;
  padding: 1rem 2rem;
  margin-right: 1rem;
  cursor: pointer;
  border-radius: 10px;
  transition: ease-in-out 0.25s;
  box-shadow: inset 0 3px 8px rgba(150, 150, 150, 0.5);
}

.manager-buttons button:hover {
  background-color: #5f2326;
  color: #fff;
  border: white solid 2px;
}

.manager-buttons button:last-child {
  margin-right: 0;
}

.logo {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  width: 140px;
  height: 140px;
  position: absolute;
  top: 8px;
  left: 10px;
  border-radius: 25px;
  border: #a93439 solid 3px;
  box-shadow: inset 0 3px 8px rgba(150, 150, 150, 0.7);
}

.search-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.search-container input {
  margin-right: 10px;
}

@font-face {
  font-family: 'Gotham Rounded Bold Italic';
  src: url(/static/media/GothamRounded-BoldItalic.1f81b501.otf) format('opentype');
  font-weight: bold;
  font-style: italic;
}

.weather {
  font-family: 'Gotham Rounded Bold Italic', sans-serif; 
  position: absolute;
  top: 20px;
  right: 20px;
}

.manager-buttons .login {
  background-color: #5a1d20;
  color: #fff;
  display: inline-block;
  padding: 10px 10px;
}

.manager-buttons .logout {
  background-color: #dc3545;
  color: #fff;
  display: inline-block;
  padding: 10px 10px;
}

.manager-buttons .profile {
  background-color: #282ea7;
  color: #fff;
  display: inline-block;
  padding: 10px 10px;
}

.tabbed-pane .tab-nav {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .tab-nav {

    justify-content: center;

  }
  
  .tabbed-pane .tab-nav li {
    display: inline-block;
    margin: 0;
    padding: 0;
  }
  
  .tabbed-pane .tab-nav button {
    display: inline-block;
    padding: 0.5rem 1rem;
    color: #333;
    text-decoration: none;
    border: 1px solid #ccc;
    border-bottom: none;
    box-shadow: inset 0 3px 8px rgba(150, 150, 150, 0.3);
  }
  
  .tabbed-pane .tab-nav button:hover {
    background-color: #f8f8f8;
    color: #a93439;
    transition: ease-in-out 0.25s;
  }

  .tabbed-pane .tab-nav button.active {
    background-color: #a93439;
    box-shadow: inset 0 3px 8px rgba(0, 0, 0, 0.3);
    color:white;
    transition: ease-in-out 0.4s;
  }
  
  .tabbed-pane .tab-content .tab-panel {
    display: none;
  }
  
  .tabbed-pane .tab-content .tab-panel.active {
    display: block;
  }

  .tab-panel {
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 10px;
    margin: auto;
  }
  
  .tab-panel button {
    width: auto;
    height: auto;
    margin: 0.1%;
    padding: 10px;
    font-size: 16px;
    background-color: white;
    border: 1px solid black;
    border-radius: 5px;
    box-sizing: border-box;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  
  .tab-panel button:hover {
    background-color: #a93439;
    color: white;
    transition: ease-in-out 0.3s;
  }
  
  .tab-panel button:active,
  .tab-panel button.active {
    background-color: #a93439;
    box-shadow: inset 0 3px 8px rgba(0, 0, 0, 0.3);
    color: #fff;
    outline: none;
    transition: ease-in-out 0.4s;
  }
.basic-head {
    display: flex;
    flex-direction: row;
    justify-content:space-evenly;
    align-items: center;
  }
  
  .basic-head img {
    height: auto;
    margin-right: 1%;
  }
  
  .basic-head h1, h1 {
    text-align: center;
    font-size: 24px;
  }
  
  .basic-head p {
    text-align: center;
    font-size: 20px;
  }
.counter {

    justify-content: center;
    display: flex;
    
}

.quant-button {

    background-color: white;
    border: maroon solid;
    border-radius: 5px;
    color: #a93439;
    transition: ease-in-out 0.25s;

}

.quant-button:hover {

    background-color: #a93439;
    color: white;

}
.bottom-buttons:disabled {

    background-color: rgb(172, 170, 170);
    color: black;
    opacity: 0.5;
    border: grey solid 2px;

}

.bottom-buttons {

    background-color: #a93439;
    color: white;
    transition: ease-in-out 0.5s;
    border: maroon solid 2px;
    border-radius: 25px;
    margin: 0 2%;
    
}

.bottom-buttons:hover:enabled {

    background-color: white;
    color: #a93439;
    transition: ease-in-out 0.25s;
    
}
